import React,{Component} from 'react';
import Delete  from '../images/delete.png'
import fire from '../auth/firebase';
import adminAuth from '../auth/adminUser';
import update from '../images/update.png';




class RecruiterDetailPrinter extends Component{

    constructor(props){

        super(props);
        this.deleteRecruiter = this.deleteRecruiter.bind(this);
        this.setUserID = this.setUserID.bind(this);
        this.handleChange = this.handleChange.bind(this)
  
        this.state={

            adminUID:'',
            newUserID:props.userID,
            FirstName:props.firstName,
            LastName:props.lastName,
            Email:props.email,
            UserType:props.userType,
           


        }
       
    }


    componentDidMount(){


        fire.database().ref("Users").orderByChild("userType").equalTo("Admin").on("value",snapshot => {

            if (snapshot.exists()){
                snapshot.forEach(child=>
                    {this.setState({adminUID:child.key})})
             
            }
            
        })
        
        
        
    }


    

    handleChange=()=>e=>{

        this.setState({[e.target.name]: e.target.value}); 


    }


    deleteRecruiter=()=>e=>{


        var confirm = window.confirm("Are you sure you want to delete the recruiter?")

        if(confirm){

        


        fire.database().ref().child("Users").child(this.state.adminUID).child("Recruiters").child(this.state.newUserID).remove();
        adminAuth.auth().deleteUser(this.state.newUserID)
        .then(function(){

            console.log("Sucessfull")

        }).catch(function(error){

            console.log(error)

        })

    }

    }

    updateRecruiter=()=>e=>{


        var postData ={
            firstName: this.state.FirstName,
            lastName:this.state.LastName,
            email: this.state.Email,
            userType: this.state.UserType,
        };
        fire.database().ref().child("Users").child(this.state.adminUID).child("Recruiters").child(this.state.newUserID).update(postData);

    }

    setUserID=input=>e=>{

        this.setState({newUserID:input});

    }

render(){


    const {firstName} = this.props;
    const {lastName} = this.props;
    const {email} = this.props;
    const {userType} = this.props;




    return(

        <>

        <hr class="my-4"></hr>
        <div className="recruiterContainer">

            <div className="row recruiterDetails">

            <div className="col-lg-3 col-md-12 col-sm-12 text-center recruiterName detailText">{firstName} {lastName} </div>
            <div className="col-lg-4 col-md-12 col-sm-12 text-center detailText">{email}</div>
            <div className="col-lg-3 col-md-12 col-sm-12 text-center detailText">{userType}</div>
            <div className="col-lg-1 col-md-12 col-sm-12 text-center "><button data-toggle="modal" data-target="#updateModal"className="deleteButton1"> <img class className="deleteIcon" src={update} alt ="delete icon"/></button></div>
            <div className="col-lg-1 col-md-12 col-sm-12 text-center "><button onClick={this.deleteRecruiter()} className="deleteButton1"><img class className="deleteIcon" src={Delete} alt ="delete icon"/></button></div>
            
            </div>

        </div>

        <div class="modal fade" id="updateModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalCenterTitle" aria-hidden="true">
            <div class="modal-dialog modal-dialog-centered" role="document">
                <div class="modal-content">
                <div class="modal-header">
                <h5 class="modal-title" id="exampleModalLongTitle">Update Recruiter</h5>
                <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                    </button>
                   
                 
                </div>
                <div class="modal-body">

                
              
                <label>First Name<input  value={this.state.FirstName} onChange={this.handleChange()} name="FirstName"type="name" className="form-control"  placeholder={firstName} required/></label>
                <label>Last Name<input  value={this.state.LastName} onChange={this.handleChange()} name="LastName"type="name" className="form-control"  placeholder={lastName} required/></label>
                <label> Email Address<input  value={this.state.Email} onChange={this.handleChange()} name="Email" type="email" className="form-control" id="exampleInputEmail1" placeholder={email} required/></label>
                
                <select  style={{width:'85%',height:'45px',backgroundColor:'rgb(0,38,76',color:'white',textAlign:'center'}}
                value={this.state.UserType}
                onChange={this.handleChange()} 
                name="UserType"
                placeholder={userType}
                >
                    <option  value="">Select User Type </option>
                    <option value="College Program">College Program</option>
                    <option value="Outreach">Division of Outreach</option>
                    <option value="General Studies">General Studies</option>
                    <option value="Pre College">Pre College</option>
                   
                    
                    
                </select>
                    
                </div>
                <div class="modal-footer">
                    <button type="button" onClick={this.updateRecruiter()} class="btn btn-primary text-center mr-auto ml-auto" data-dismiss="modal">Confirm Changes</button>
                </div>
                </div>
            </div>
            </div>



    





        </>








    )
}


}
export default RecruiterDetailPrinter;